.fon {
  width: 100%;
  height: rem(480px);
  margin-top: rem(50px);
  background-image:  url_css('img/dsc-4716.png');
  background-repeat:no-repeat;
  position: relative;
  top:0;
  left: 0;

    .fon_item{
      width: 100%;
      height:inherit;
      background-color: #ffffff;
      position: absolute;
      top: 0;
      left:0;
      opacity: 0.9;
      text-align: center;
      display: flex;

    }

  .form_block {
    position: relative;
    top: 0;
    left: 0;
    z-index: 1;
    background: #ffffff;
    max-width: rem(665px);
    box-shadow: 0 11px 16px rgba(1, 1, 1, 0.1);
    display: flex;

    .my_form {
      display: flex;
      flex-direction: column;
      margin: 0 auto;
      .inp {
        width: rem(435px);
        height: rem(50px);
        border: none;
        text-align: center;
        margin-top: rem(20px);
        color: #252a2d;
        font-family: "Gotham Pro";
        font-size: 17px;
        font-weight: 300;
        line-height: 1.6;
      }
      textarea {
        box-shadow: inset 0 1px 24px rgba(1, 1, 1, 0.03);
        background-color: #ffffff;
        margin-top: rem(20px);
        color: #252a2d;
        text-align: center;
        font-family: "Gotham Pro";
        font-size: 17px;
        font-weight: 300;
        line-height: 1.6;
        border: none;
      }
      .form_login {
        box-shadow: inset 0 1px 24px rgba(1, 1, 1, 0.03);
        background-color: #ffffff;
      }
      .form_email {
        box-shadow: inset 0 1px 24px rgba(1, 1, 1, 0.09);
        background-color: #fcfcfc;
      }
      .form_tema {
        box-shadow: inset 0 1px 24px rgba(1, 1, 1, 0.03);
        background-color: #ffffff;
      }
      .form_submit{
        width: rem(250px);
        height: rem(50px);
        box-shadow: 0 1px 13px rgba(64, 184, 235, 0.36);
        border-radius: 24px;
        border: 1px solid #40b8eb;
        background: #ffffff;
        margin-top: rem(60px);
        color: #40b8eb;
        font-family: Intro;
        font-size: 16px;
        font-weight: 400;
        line-height: 1.75;
        margin: rem(50px) auto;
      }

    }
  }
}
#map{
  width: 100%;
  height: 100vh;



}


